import React from "react";
import { render } from "react-dom";
import { TextInput, TextArea } from "carbon-components-react";
import "./style.css";

const App = () => (
  <div className="container">
    <TextInput
      type="text"
      id="test-input"
      labelText="Test input"
      // value should be passed down to the input element
      value="This is a text value"
    />
    <TextArea
      type="text"
      id="test-textarea"
      labelText="Test textarea"
      // value should be passed down to the textarea element
      value="This is a text value"
    />
  </div>
);

render(<App />, document.getElementById("root"));
